.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  &__inner {
    max-width: 1160px;
    width: 100%;
    margin: 0 auto;
    @extend %flex-v;
  }
  &__top {
    background: $black;
    height: 35px;
    @extend %flex-v;
    .header__inner {
      justify-content: flex-end;
    }
  }
  &__top-nav {
    ul {
      @extend %list;
      @extend %flex-v;
      li {
        font-weight: 600;
        font-size: 12px;
        line-height: 12px;
        margin-right: 30px;
        a {
          color: #fff;
          text-decoration: none;
          transition: .3s ease-in-out;
          @include hover() {
            color: $brown;
          }
        }
      }
    }
  }
  &__top-controls {
    @extend %flex-v;
  }
  &__top-control {
    font-weight: 600;
    font-size: 12px;
    &.login {
      position: relative;
      margin-right: 24px;
      &::after {
        content: '';
        position: absolute;
        right: -12px;
        top: 50%;
        transform: translateY(-50%);
        width: 4px;
        height: 4px;
        border-radius: 50%;
        background: #fff;
      }
    }
    a {
      @extend %flex-v;
      color: #fff;
      text-decoration: none;
      transition: .3s ease-in-out;
      svg {
        fill: #fff;
        margin-right: 8px;
        transition: .3s ease-in-out;
      }
      @include hover() {
        color: $brown;
        svg {
          fill: $brown;
        }
      }
    }
  }
  &__main {
    background: #fff;
    box-shadow: 0px 5px 75px rgba(0, 0, 0, 0.075);
    @extend %flex-v;
    height: 80px;
  }
}

.main-nav {
  margin-left: auto;
  ul {
    @extend %list;
    @extend %flex-v;
    margin: 0 -22px;
    li {
      font-size: 14px;
      margin: 0 22px;
      a {
        color: $black;
        text-decoration: none;
        transition: .3s ease-in-out;
        @include hover() {
          color: $green;
        }
      }
    }
  }
}

.search {
  margin-left: 45px;
  &__input {
    display: none;
  }
  &__button {
    @extend %flex-c;
    width: 32px;
    height: 30px;
    border: 1px solid #5a5e62;
    border-radius: 5px;
    cursor: pointer;
    transition: .3s ease-in-out;
    @include hover() {
      background: $brown;
      border-color: $brown;
    }
  }
}