.bottle-head-wrapper {
  background: #f9f9fa;
  padding: 50px 15px;
}
.bottle-heading {
  max-width: 1160px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 50px;
  &__inner {
    width: 100%;
    max-width: 380px;
  }
  &__title {
    font-family: 'Playfair Display', serif;
    font-weight: 700;
    font-size: 40px;
    margin-bottom: 20px;
  }
  &__top {
    margin-bottom: 40px;
  }
  &__info {
    width: 100%;
    max-width: 300px;
    padding: 30px;
    background: #0b1014;
    border-radius: 5px;
    color: #fff;
    &-top {
      display: flex;
      align-items: center;
      gap: 15px;
      margin-bottom: 20px;
    }
    &-img {
      border-radius: 5px;
      overflow: hidden;
    }
    &-title {
      font-family: 'Playfair Display', serif;
      font-weight: 700;
      font-size: 16px;
    }
    &-content {
      font-size: 14px;
      line-height: 175%;
      margin-bottom: 20px;
    }
    &-link {
      font-weight: 500;
      font-size: 14px;
      text-decoration: none;
      color: #deb992;
    }
  }
}
.specification {
  width: 100%;
  td {
    padding: 10px 0;
    border-bottom: 1px solid #ebebee;
  }
  tr td:first-child {
    color: #5a5e62;
  }
}

.rating {
  &__inner {
    display: flex;
    gap: 10px;
    margin-bottom: 10px;
  }
  &__stars {
    display: flex;
    gap: 3px;
  }
  &__count {
    font-weight: 600;
    font-size: 18px;
  }
  &__reviews {
    font-size: 14px;
    color: #5a5e62;
    display: inline-flex;
    gap: 10px;
    align-items: center;
    text-decoration: none;
  }
}