.main-section {
  width: 100%;
  height: calc(100vh - 115px);
  background: #f9f9fa;
  @extend %flex-v;
  margin-bottom: 150px;
  &__inner {
    max-width: 900px;
    width: 100%;
    margin: 0 auto;
  }
  &__title {
    max-width: 560px;
    width: 100%;
    font-family: $Tfont;
    font-weight: 700;
    font-size: 54px;
    line-height: 125%;
    text-align: center;
    margin: 0 auto 75px;
  }
  &__description {
    font-weight: 500;
    font-size: 20px;
    line-height: 150%;
    text-align: center;
    margin-bottom: 45px;
  }
  &__box {
    width: 100%;
    background: #fff;
    border: 1px solid #ebebee;
    box-shadow: 0px 5px 75px rgba(0, 0, 0, 0.075);
    border-radius: 5px;
    padding: 40px 75px;
  }
  &__search {
    position: relative;
    margin-bottom: 35px;
  }
  &__search-input {
    padding: 0 25px;
    width: 100%;
    height: 75px;
    background: #ebebee;
    border-radius: 5px;
    font-size: 18px;
    border: none;
    &::placeholder {
      color: #5a5e62;
    }
  }
  &__search-submit {
    position: absolute;
    right: 15px;
    top: 50%;
    transform: translateY(-50%);
    max-width: 110px;
    width: 100%;
    height: 56px;
    @extend %flex-c;
    font-weight: 500;
    font-size: 14px;
    color: #fff;
    background: $green;
    border-radius: 5px;
    border: none;
    cursor: pointer;
    transition: .3s ease-in-out;
    svg {
      margin-left: 10px;
    }
    @include hover() {
      background: $black;
    }
  }
  &__tags {
    @extend %flex-v;
  }
  &__tags-title {
    font-weight: 400;
    font-size: 12px;
    line-height: 12px;
    color: $gray;
    margin-right: 26px;
  }
  &__tags-list {
    @extend %list;
    @extend %flex-v;
    margin: 0 -7px;
    li {
      font-weight: 500;
      font-size: 14px;
      margin: 0 7px;
      a {
        color: $black;
        text-decoration: none;
        padding-bottom: 5px;
        border-bottom: 2px solid #ebebee;
        transition: .3s ease-in-out;
        @include hover() {
          color: $green;
          border-color: $green;
        }
      }
    }
  }
  &__tags-button {
    margin-left: 20px;
    cursor: pointer;
  }
}

.main-content {
  max-width: 1160px;
  width: 100%;
  margin: 0 auto 150px;
  @extend %flex-v;
  justify-content: space-between;
  &__image {
    max-width: 560px;
    width: 100%;
    flex-basis: 560px;
    text-align: center;
    margin: 0 40px 0 0;
  }
  &__content {
    max-width: 430px;
    width: 100%;
    flex-basis: 430px;
  }
  &__title {
    font-family: $Tfont;
    font-weight: 700;
    font-size: 32px;
    margin-bottom: 30px;
  }
  &__text {
    font-weight: 400;
    font-size: 16px;
    line-height: 175%;
    p {
      @include marginY()
    }
  }
  &--reverse {
    flex-direction: row-reverse;
    .main-content__image {
      margin: 0 0 0 40px;
    }
  }
}

.holder-block {
  width: 100%;
  height: 38px;
  background-size: cover;
  background-repeat: no-repeat;
}

.brands-info {
  max-width: 1160px;
  width: 100%;
  margin: 0 auto;
  padding: 100px 0 90px;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  &__title {
    max-width: 350px;
    width: 100%;
    flex-basis: 350px;
    font-weight: 600;
    font-size: 32px;
    margin-right: 80px;
  }
  &__text {
    max-width: 730px;
    width: 100%;
    flex-basis: 730px;
    font-size: 16px;
    line-height: 175%;
    span {
      color: $green;
    }
  }
}

.brands {
  max-width: 1240px;
  width: 100%;
  margin: 0 auto 30px;
  &__item {
    display: flex;
    align-items: stretch;
    width: 100%;
    height: 360px;
    background: #fff;
    border: 1px solid #ebebee;
    border-radius: 0px 5px 5px 5px;
    margin-bottom: 20px;
    transition: .3s ease-in-out;
    @include hover() {
      border-color: $brown;
      .brands__item-number {
        background: $brown;
      }
      .brands__item-star {
        visibility: visible;
        opacity: 1;
        overflow: visible;
      }
    }
  }
  &__item-left {
    position: relative;
    max-width: 300px;
    width: 100%;
    flex-basis: 300px; 
  }
  &__item-middle {
    max-width: 640px;
    width: 100%;
    flex-basis: 640px;
    padding: 50px 40px;
    border-left: 1px solid  #ebebee;
    border-right: 1px solid  #ebebee;
  }
  &__item-right {
    position: relative;
    max-width: 300px;
    width: 100%;
    flex-basis: 300px;
    background: #f9f9fa;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  &__item-text {
    font-size: 15px;
    line-height: 175%;
    overflow: hidden;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 10;
    p {
      @include marginY()
    }
  }
  &__item-title {
    position: relative;
    @extend %flex-c;
    width: 100%;
    height: 130px;
    background: #f9f9fa;
    font-weight: 600;
    font-size: 24px;
    &::before {
      content: '';
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      height: 4px;
      width: 100px;
      bottom: 0;
      background: $gray;
    }
  }
  &__item-image {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    padding-bottom: 50px;
  }
  &__item-number {
    @extend %flex-c;
    position: absolute;
    background: $black;
    width: 42px;
    height: 42px;
    right: 100%;
    top: 0;
    border-radius: 5px 0px 0px 5px;
    font-weight: 600;
    font-size: 22px;
    line-height: 150%;
    color: #fff;
    transition: .3s ease-in-out;
  }
  &__item-count {
    font-weight: 500;
    font-size: 14px;
    margin-bottom: 65px;
    span {
      font-weight: 600;
      font-size: 24px;
      vertical-align: middle;
      margin-left: 10px;
    }
    small {
      font-weight: 500;
      font-size: 18px;
      margin-left: 15px;
      vertical-align: super;
    }
  }
  &__item-rate {
    font-weight: 600;
    font-size: 24px;
    margin-bottom: 5px;
  }
  &__item-rate-info {
    font-size: 14px;
    line-height: 14px;
    text-align: center;
    color: $gray;
    margin-bottom: 28px;
  }
  &__item-button {
    @extend %flex-c;
    max-width: 185px;
    width: 100%;
    height: 44px;
    background: $black;
    border-radius: 5px;
    font-size: 14px;
    color: #fff;
    text-decoration: none;
    border: none;
    cursor: pointer;
    transition: .3s ease-in-out;
    svg {
      margin-left: 10px;
    }
    @include hover() {
      background: rgba(11, 16, 20, 0.7)
    }
  }
  &__item-star {
    position: absolute;
    right: 0;
    top: 0;
    @extend %flex-c;
    width: 50px;
    height: 50px;
    background: rgba(222, 185, 146, 0.5);
    border-radius: 0px 0px 0px 5px;
    visibility: hidden;
    opacity: 0;
    overflow: hidden;
    transition: .3s ease-in-out;
  }
}

.rate {
  position: relative;
  display: flex;
  margin-bottom: 15px;
  &__item {
    flex: 0 0 auto;
    margin-right: 5px;
    fill: rgba(90, 94, 98, 0.4);
    [data-rate='1'] & {
      &:nth-child(1) {
        fill: $green;
      }
    }
    [data-rate='2'] & {
      &:nth-child(1),
      &:nth-child(2) {
        fill: $green;
      }
    }
    [data-rate='3'] & {
      &:nth-child(1),
      &:nth-child(2),
      &:nth-child(3) {
        fill: $green;
      }
    }
    [data-rate='4'] & {
      &:nth-child(1),
      &:nth-child(2),
      &:nth-child(3),
      &:nth-child(4) {
        fill: $green;
      }
    }
    [data-rate='5'] & {
      &:nth-child(1),
      &:nth-child(2),
      &:nth-child(3),
      &:nth-child(4),
      &:nth-child(5) {
        fill: $green;
      }
    }
  }
}

.brands-about {
  padding: 94px 0 100px;
  &__inner {
    max-width: 900px;
    width: 100%;
    margin: 0 auto 50px;
  }
  &__title {
    font-weight: 600;
    font-size: 24px;
    margin-bottom: 50px;
  }
  &__text {
    font-size: 16px;
    line-height: 175%;
  }
  &__list {
    padding: 35px 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-top: 1px solid  #ebebee;
    border-bottom: 1px solid  #ebebee;
    margin: 0 -35px;
  }
  &__item {
    margin: 0 35px;
  }
}

.best-products {
  background: #f9f9fa;
  padding: 100px 0 60px;
  &__inner {
    max-width: 1160px;
    width: 100%;
    margin: 0 auto;
  }
  &__head {
    @extend %flex-v;
    justify-content: space-between;
    margin-bottom: 65px;
  }
  &__content {
    max-width: 350px;
    width: 100%;
    flex-basis: 350px;
    margin-right: 30px;
  }
  &__title {
    font-weight: 600;
    font-size: 32px;
  }
  &__caption {
    font-weight: 500;
    font-size: 20px;
    color: $pink;
  }
  &__text {
    max-width: 730px;
    width: 100%;
    flex-basis: 730px;
    font-size: 16px;
    line-height: 175%;
  }
  &__list {
    display: flex;
    align-items: stretch;
    flex-wrap: wrap;
    margin: 0 -20px;
  }
  &__item {
    position: relative;
    max-width: calc(25% - 40px);
    width: 100%;
    flex-basis: calc(25% - 40px);
    margin: 0 20px 40px;
    height: 200px;
    background: #fff;
    border: 1px solid #ebebee;
    border-radius: 5px;
    padding: 35px 35px;
    text-decoration: none;
    overflow: hidden;
    cursor: pointer;
    transition: .3s ease-in-out;
    @include hover() {
      border-color: $green;
      .best-products__item-title {
        text-decoration: underline;
      }
    }
  }
  &__item-box {
    max-width: 140px;
    width: 100%;
  }
  &__item-title {
    font-family: $Tfont;
    font-weight: 700;
    font-size: 16px;
    color: $black;
    margin-bottom: 15px;
  }
  &__item-rate {
    font-weight: 600;
    font-size: 18px;
    color: $black;
    line-height: 175%;
  }
  &__item-info {
    font-size: 13px;
    line-height: 13px;
    color: #5a5e62;
  }
  &__item-image {
    position: absolute;
    bottom: 0;
    right: 20px;
  }
}